body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  font: 20px;
  padding: 2.5%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.profile {
  width: 200px;
  height: 200px;
  border-radius: 100px; /*half of width and height give circle */
}

.title-fade-out {
  opacity: 0;
  animation-name: fade-out;
  animation-duration: 1s;
}

.title-fade-in {
  opacity: 100;
  animation-name: fade-in;
  animation-duration: 1s;
}


@keyframes fade-out { /* Keyframes is special css class */

  from {opacity: 100}
  to {opacity: 0;}
}

@keyframes fade-in {
  from {opacity: 0}
  from {opacity: 100}
}